import Footer from '../../containers/Footer1'
import MainFooter from '../../containers/Footer2'
import './SinglePostPage.scss'
import Image1 from './images/1-image.png'
import Image2 from './images/2-image.png'
import Image3 from './images/3-image.png'
import Image4 from './images/4-image.png'
import Image5 from './images/5-image.png'
import MainImage from './images/main-image.png'
import { useState } from 'react'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'
import WatchedArr from '../HomePage/WatchedArr/WatchedArr'
import Post from '../../components/Post'

const SinglePostPage = () => {

    const [inputStatus, setInputStatus] = useState(true)
    const [inputStatus2, setInputStatus2] = useState(true)
    const [status, setStatus] = useState(true)
    return (
        <>
            <div className="single-post-wrapper">
                <Helmet>
                    <title>Qiwi | Single Post</title>
                </Helmet>
                <div className="container">
                    <span className="way">
                        Объявления   /   Ташкент Недвижимость   /   Ташкент Квартиры   /   Ташкент Продажа   /   Новостройки Ташкент
                </span>
                    <div className="subtitle">
                        <h1>Срочно Голден Хаус Етти Чинор Новостройка 4х ком на 1 этаже юнусабад 5</h1>
                    </div>
                    <div className="about-post">
                        <div className="left">
                            <div className="little-images">
                                <img src={Image1} alt="" />
                                <img src={Image2} alt="" />
                                <img src={Image3} alt="" />
                                <img src={Image4} alt="" />
                                <img src={Image5} alt="" />
                            </div>
                            <div className="main-image">
                                <img src={MainImage} alt="" />
                            </div>
                        </div>
                        <div className="right">
                            <div className="id-post">
                                <span>Артикул: 4418970</span>
                                <span>обновлено: 28.04.17 добавлено: 28.04.17</span>
                            </div>
                            <h2 className="price-post">
                                70 000 у.е.
                        </h2>
                            <a href="#" className="location">
                                Ташкент, Ташкентская область, Юнусабадский район
                        </a>
                            <div className="number-wrapper">
                                <div className="wrapper">
                                    <div className="left-wrapper">
                                        <p>99891 166</p>
                                        <input type={inputStatus === true ? 'password' : 'number'} value='7855' disabled />
                                    </div>
                                    <div className="right-wrapper">
                                        <button className='show' onClick={() => setInputStatus(!inputStatus)} >
                                            Покозать польностю
                                    </button>
                                    </div>
                                </div>
                            </div>
                            <div className="number-wrapper">
                                <div className="wrapper">
                                    <div className="left-wrapper">
                                        <p>99891 166</p>
                                        <input type={inputStatus2 === true ? 'password' : 'number'} value='7855' disabled />
                                    </div>
                                    <div className="right-wrapper">
                                        <button className='show' onClick={() => setInputStatus2(!inputStatus2)} >
                                            Покозать польностю
                                    </button>
                                    </div>
                                </div>
                            </div>
                            <p className='author-post'>
                                Автор обьявлении: <a href="#">Зухриддин Темиров</a>
                            </p>
                            <div className="button-group">
                                <Link to='/chatting' className='button'>
                                    Написать автору
                            </Link>
                                <Link to='/' className='button'>
                                    Предложить свою цену
                            </Link>
                            </div>
                            <table className='table'>
                                <tr>
                                    <td className='top'>
                                        <span>Количество комнат:</span>
                                        <span>6</span>
                                    </td>
                                    <td className='top'>
                                        <span>Общая площадь:</span>
                                        <span>40 м2</span>
                                    </td>
                                    <td className='top right'>
                                        <span>Этаж:</span>
                                        <span>32</span>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <span>Этажность дома:</span>
                                        <span>6</span>
                                    </td>
                                    <td>
                                        <span>Состояние квартиры:</span>
                                        <span>Первая сдача </span>
                                    </td>
                                    <td className='right'>
                                        <span>Этаж:</span>
                                        <span>32</span>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <span>Планировка</span>
                                        <a>Раздельная</a>
                                    </td>
                                    <td>
                                        <span>Год постройки/сдачи</span>
                                        <a href="#">Сдача в 2017</a>
                                    </td>
                                    <td className='right'>
                                        <span>Ремонт</span>
                                        <span>Авторский проект</span>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <span>Санузел</span>
                                        <a href="#">2 санузла и более</a>
                                    </td>
                                    <td>
                                        <span>Меблирована</span>
                                        <a href="#">Да</a>
                                    </td>
                                    <td className='right'>
                                        <span>Высота потолков</span>
                                        <span>32</span>
                                    </td>
                                </tr>
                                <tr >
                                    <td colSpan='3' className='last right bottom'>
                                        <span>Рядом есть</span>
                                        <a href="#">Больница, поликлиника, Детская, площадка, Детский сад, Остановки, Парк, зелёная зона, Развлекательные заведения, Рестораны, кафе	</a>
                                    </td>
                                </tr>
                            </table>
                            <p className="overwiew">
                                <span>
                                    Срочно продаётся 3х ком на 1 этоже 7 этажном кирпичном доме. Новостройка Голден Хаус жылой комплекс Етти Чинор Очень качественный и дорогой ремонт. Комнаты полнастю разделные болшые. 2 сан узла. Для лишних вещей есть кладовка.Ремонт шыкарный обсалютно новый.
                            </span>
                                <span>
                                    Ремонт делолся из очень дорогова материяла. Дорогая класическая кухонная мебель. Джаккузи,Тёплые полы в каждой комнате. Дорогие люстры вовсех комнатах решодки на окнах. Теретория охранается. Всё предусмотренно для детей деские площядки качели итд. Торг наместе. Торопитесь продаю нужны денги. Первый золотой этаж и паследний в этом коплексе. Торопитесь.
                            </span>
                            </p>
                        </div>
                    </div>
                </div>
                <section className="extra-post">
                    <div className="container">
                        <div className="post-type">
                            <div className="top">
                                <button onClick={() => setStatus(true)} className={`post-status ${status === true ? 'active' : ' '}`}>Похожие объявления</button>
                                <button onClick={() => setStatus(false)} className={`post-status ${status === false ? 'active' : ' '}`}>Обьявление автора</button>
                            </div>
                            <div className="bottom">
                                <div className={`active-new ${status === true ? 'active' : ' '}`}></div>
                                <div className={`active-best ${status === false ? 'active' : ' '}`}></div>
                                <div className="line"></div>
                            </div>
                        </div>
                        <div className="article">
                            {
                                status === true ? (
                                    <>
                                        <div className="subtitle">
                                            <h1>Похожие объявления</h1>
                                        </div>
                                        {
                                            WatchedArr.map(item => (
                                                <Post
                                                    img={item.image}
                                                    name={item.name}
                                                    time={item.date}
                                                    price={item.price}
                                                />
                                            ))
                                        }
                                    </>
                                ) : (
                                    <>
                                        <div className="subtitle">
                                            <h1>Обьявление автора</h1>
                                        </div>
                                        {
                                            WatchedArr.map(item => (
                                                <Post
                                                    img={item.image}
                                                    name={item.name}
                                                    time={item.date}
                                                    price={item.price}
                                                />
                                            ))
                                        }
                                    </>
                                )
                            }
                        </div>
                    </div>
                </section>
                <Footer />
                <MainFooter />
            </div>
        </>
    )
}

export default SinglePostPage